import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";
import Table from "@mui/material/Table";
import TableBody from "@mui/material/TableBody";
import TableCell from "@mui/material/TableCell";
import TableContainer from "@mui/material/TableContainer";
import TableHead from "@mui/material/TableHead";
import TableRow from "@mui/material/TableRow";
import Paper from "@mui/material/Paper";
import { styled } from "@mui/system";
import { Button } from "@mui/material";
import "../assets/Quick.css";

const TableContainerStyled = styled(TableContainer)`
  margin: 0;
  padding: 0;
`;

const TableCellStyled = styled(TableCell)`
  margin: 0;
  padding: 8px 5px;
  text-align: center;

  @media (max-width: 768px) {
    font-size: 11px; /*   600px     */

    &:first-child {
      //border: 1px solid black;
      width: 20%;
    }
  }

  @media (min-width: 768px) {
    font-size: 13px; /*   601px     */
    height: 20px;

    &:first-child {
      //border: 1px solid black;
      width: 20%;
    }

    &:nth-child(2) {
      //border: 1px solid black;
    }
    &:nth-child(3) {
      //border: 1px solid black;
    }
  }
`;

function createData(name, calories, fat) {
  return { name, calories, fat };
}

const rows = [
  createData("", "1160  ", "1600  "),
  createData("", "1160   1260 ", "1600   1750 "),
  createData("", "1260   1360 ", "1750   1900 "),
  createData("", "1360   1430 ", "1900   2000 "),
  createData("", "1430 ", "2000 "),
];

const Quick = () => {
  const [isScrolled, setIsScrolled] = useState(false);

  const handleScroll = () => {
    setIsScrolled(window.scrollY > 2000);
  };

  useEffect(() => {
    window.addEventListener("scroll", handleScroll);

    return () => {
      window.removeEventListener("scroll", handleScroll);
    };
  }, []);

  return (
    <>
      <div className={`quick-menu ${isScrolled ? "scrolled" : ""}`}>
        <div className="quickmenu">
          <div className="msg">
            *      ( : 5)
          </div>
          <TableContainer component={Paper}>
            <Table aria-label="simple table" sx={{ margin: 0, padding: 0 }}>
              <TableHead>
                <TableRow
                  sx={{
                    "&:last-child td, &:last-child th": {
                      fontSize: { xs: "10px", tab: "12px" },
                      fontWeight: "bold",
                      borderTop: "none",
                    },
                  }}
                >
                  <TableCellStyled> </TableCellStyled>
                  <TableCellStyled align="center">
                    &nbsp;1&nbsp;
                  </TableCellStyled>
                  <TableCellStyled align="center">
                    &nbsp;2&nbsp;
                  </TableCellStyled>
                </TableRow>
              </TableHead>
              <TableBody>
                {rows.map((row) => (
                  <TableRow key={row.name}>
                    <TableCellStyled component="th" scope="row">
                      <Button
                        variant="contained"
                        size="small"
                        style={{
                          fontSize: "12px",
                          backgroundColor:
                            row.name === ""
                              ? "red"
                              : row.name === ""
                              ? "orange"
                              : row.name === ""
                              ? "yellow"
                              : row.name === ""
                              ? "lightgreen"
                              : row.name === ""
                              ? "skyblue"
                              : "none",
                          color: "black",
                        }}
                      >
                        {row.name}
                      </Button>
                    </TableCellStyled>
                    <TableCellStyled align="center">
                      {row.calories}
                    </TableCellStyled>
                    <TableCellStyled align="center">{row.fat}</TableCellStyled>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </TableContainer>
        </div>
      </div>
    </>
  );
};

export default Quick;
