import { Box, Modal, Typography } from "@mui/material";
import * as React from "react";

const modalstyle = {
  position: "absolute",
  top: "50%",
  left: "50%",
  transform: "translate(-50%, -50%)",
  width: 400,
  bgcolor: "background.paper",
  border: "2px solid #000",
  boxShadow: 24,
  p: 4,
};

export default function WeatherModal({ open, handleClose, flight }) {
  function formatDateTime(dateTimeStr) {
    if (dateTimeStr) {
      const [datePart, timePart] = dateTimeStr.split("T");
      const timeOnly = timePart.slice(0, 5);
      return `${datePart}, ${timeOnly}`;
    }
  }

  console.log("ghkdkjfsl", flight);
  if (flight) {
    console.log("");
  } else {
    console.error("  airport   .");
  }

  return (
    <Modal
      open={open}
      onClose={handleClose}
      aria-labelledby="modal-modal-title"
      aria-describedby="modal-modal-description"
    >
      <Box sx={modalstyle}>
        <Typography id="modal-modal-title" variant="h6" component="h2">
           "{flight.airport}"   
        </Typography>
        <Typography id="modal-modal-title" variant="h6" component="h2">
             : {flight.scheduleDateTime}
        </Typography>
        <Typography id="modal-modal-description" sx={{ mt: 2 }}>
          <p>
            {" "}
             : <img src={flight.wimage} />
          </p>

          <p>
            {" "}
            <span> : {flight.senstemp}</span>
            {" // "}
            <span> : {flight.temp}</span>
          </p>
          <p>  : {flight.wind}</p>
          <p> </p>
          <p> </p>
        </Typography>
      </Box>
    </Modal>
  );
}
