import * as React from "react";
import Table from "@mui/material/Table";
import TableBody from "@mui/material/TableBody";
import TableCell from "@mui/material/TableCell";
import TableContainer from "@mui/material/TableContainer";
import TableHead from "@mui/material/TableHead";
import TableRow from "@mui/material/TableRow";
import Paper from "@mui/material/Paper";
import { styled } from "@mui/system";
import { Button } from "@mui/material";

const TableContainerStyled = styled(TableContainer)`
  margin: 0;
  padding: 0;
`;

function createData(name, calories, fat) {
  return { name, calories, fat };
}

const rows = [
  createData("", "1160  ", "1600  "),
  createData("", "1160   1260 ", "1600   1750 "),
  createData("", "1260   1360 ", "1750   1900 "),
  createData("", "1360   1430 ", "1900   2000 "),
  createData("", "1430 ", "2000 "),
];

export default function CongestionTable() {
  return (
    // <TableContainerStyled component={Paper}>
    <TableContainer component={Paper}>
      {/* <span>   </span> */}
      <Table
        aria-label="simple table"
        sx={{ margin: 0, padding: 0, width: 650 }}
      >
        <TableHead>
          <TableRow
            sx={{
              "&:last-child td, &:last-child th": {
                fontSize: { xs: "12px", tab: "12px" },
                fontWeight: "bold",
                borderTop: "none",
              },
            }}
          >
            <TableCell sx={{ width: "80px" }}> </TableCell>
            <TableCell align="center">&nbsp;1&nbsp;</TableCell>
            <TableCell align="center">&nbsp;2&nbsp;</TableCell>
          </TableRow>
        </TableHead>
        <TableBody>
          {rows.map((row) => (
            <TableRow
              key={row.name}
              sx={{
                "&>*": {
                  fontSize: { xs: "11px", tab: "13px" },
                  height: "10px",
                  margin: 0,
                  padding: "5px 15px",
                },
              }}
            >
              <TableCell component="th" scope="row">
                <Button
                  variant="contained"
                  size="small"
                  style={{
                    fontSize: "12px",
                    backgroundColor:
                      row.name === ""
                        ? "red"
                        : row.name === ""
                        ? "orange"
                        : row.name === ""
                        ? "yellow"
                        : row.name === ""
                        ? "lightgreen"
                        : row.name === ""
                        ? "skyblue"
                        : "none",
                    color: "black",
                  }}
                >
                  {row.name}
                </Button>
              </TableCell>
              <TableCell align="center">{row.calories}</TableCell>
              <TableCell align="center">{row.fat}</TableCell>
            </TableRow>
          ))}
        </TableBody>
      </Table>
    </TableContainer>
  );
}
