function getToday() {
  let date = new Date();

  //      JSON  
  return {
    year: date.getFullYear(),
    month: ("0" + (1 + date.getMonth())).slice(-2),
    day: ("0" + date.getDate()).slice(-2),
    hours: ("0" + date.getHours()).slice(-2),
    minutes: ("0" + date.getMinutes()).slice(-2),
    seconds: ("0" + date.getSeconds()).slice(-2),
  };
}

export default getToday();
